<template>
  <div>
    <div class="vue" v-for="item in list">

      <div class="title">{{item.className}}</div>
      <div class="userInfo">
        <div class="cont">
          <div class="type">姓名</div>
          <div class="content">{{item.userName}}</div>
        </div>
        <div class="cont">
          <div class="type">联系电话</div>
          <div class="content">{{item.phoneNumber}}</div>
        </div>
        <div class="cont">
          <div class="type">预约时间</div>
          <div class="content">{{item.dateTime}}</div>
        </div>
      </div>
      <a class="classInfo" @click="goDeatil">
        <div class="name">{{item.schoolName}}</div>
        <div class="address">{{item.address}}</div>
      </a>

    </div>
  </div>

</template>

<script>
    import {Indicator} from 'mint-ui';

    export default {
        data(){
          return {
              list: []
          }
        },
        mounted() {
          if(window.window.sessionStorage.myClass){
            this.list = JSON.parse(window.window.sessionStorage.myClass);
          }else {
            Indicator.open({ spinnerType: 'triple-bounce' });
            this.getInfo();
          }

        },
        methods: {
          getInfo(){
            let that = this;

            //mock ajax
            setTimeout(()=>{
              let list = [
                  {
                      className: '仙翁操',
                      userName: '渣璐',
                      phoneNumber: '15029378321',
                      dateTime: '6.14-6.18',
                      schoolName: '赏清河学堂',
                      address: '风光路商贸大厦4号楼2单元1101'
                  },
                  {
                      className: '仙翁操',
                      userName: '象拔蚌',
                      phoneNumber: '15029378321',
                      dateTime: '6.14-6.18',
                      schoolName: '赏清河学堂',
                      address: '风光路商贸大厦4号楼2单元1101'
                  },
                  {
                      className: '仙翁操',
                      userName: '小西瓜',
                      phoneNumber: '15029378321',
                      dateTime: '6.14-6.18',
                      schoolName: '赏清河学堂',
                      address: '风光路商贸大厦4号楼2单元1101'
                  },
              ];
              Indicator.close();
              that.list = list;
              window.window.sessionStorage.myClass = JSON.stringify(list);
            },3000);
          },
          goDeatil(){
            this.$router.push({
              path: 'classDetail'
            })
          }
        }
    }
</script>

<style lang="scss" scoped>

.vue{
  height: 2.3rem;
  background: #fff;
  padding: 0 0.1rem;
}
.title{
  line-height: 0.5rem;
  font-size: 0.16rem;
  color: #bd8747;
  text-align: center;
}
.userInfo{
  border-top: 0.01rem solid #ddd;
  border-bottom: 0.01rem solid #ddd;
  padding: 0.075rem 0;
  .cont{
    line-height: 0.3rem;
    div{
      display: inline-block;
      text-indent: 0.05rem;
    }
    .type{
      font-size: 0.13rem;
      color: #999;
      width: 0.82rem;
    }
    .content{
      font-size: 0.15rem;
      color: #333;
    }
  }

}
.classInfo{
  padding-top: 0.1rem;
  background: url('../assets/images/right.png') no-repeat 3.4rem center;
  background-size: 0.18rem;
  .name{
    line-height: 0.25rem;
    font-size: 0.15rem;
    color: #333;
  }
  .address{
    line-height: 0.23rem;
    font-size: 0.13rem;
    color: #999;
  }
}
</style>



